.swipe-page {
  min-height: calc(100vh - clamp(80px, 15vw, 100px));
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  display: flex;
  flex-direction: column;
}

@media (max-width: 768px) {
  .swipe-page {
    min-height: calc(100vh - clamp(50px, 12vw, 70px));
    padding-bottom: clamp(50px, 12vw, 70px);
  }
}

.swipe-container {
  max-width: min(700px, 100vw);
  margin: 0 auto;
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: clamp(0.5rem, 2vw, 0.75rem) clamp(0.75rem, 3vw, 1.5rem) clamp(0.75rem, 2vw, 1rem);
}

.swipe-header {
  text-align: center;
  margin-bottom: auto;
  padding-top: clamp(0.5rem, 2vw, 1rem);
}

.swipe-header h1 {
  font-size: clamp(1.5rem, 5vw, 2rem);
  color: #333;
  margin: 0 0 clamp(0.25rem, 0.75vw, 0.375rem) 0;
  font-weight: 700;
}

.swipe-header p {
  color: #666;
  font-size: clamp(0.85rem, 2.5vw, 0.95rem);
  margin: 0;
}

.swipe-progress {
  text-align: center;
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  color: #888;
  font-size: clamp(0.8rem, 2vw, 0.9rem);
}

.empty-state {
  text-align: center;
  padding: clamp(2rem, 8vw, 4rem) clamp(1rem, 4vw, 2rem);
  background: white;
  border-radius: clamp(16px, 4vw, 24px);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  max-width: min(500px, 90vw);
  margin: auto;
}

.empty-icon {
  font-size: clamp(3rem, 10vw, 5rem);
  margin-bottom: clamp(0.75rem, 2vw, 1rem);
}

.empty-state h2 {
  color: #333;
  margin: 0 0 clamp(0.75rem, 2vw, 1rem) 0;
  font-size: clamp(1.5rem, 4vw, 1.75rem);
}

.empty-state p {
  color: #666;
  line-height: 1.6;
  margin: 0;
  font-size: clamp(0.9rem, 2.5vw, 1rem);
}
